#game {
    background-color: darkcyan;
    width: 800px;
    margin: auto;
}


#title {
    text-align: center;
    margin-top: 30px;
}

#rules {
    text-align: center;
    max-width: 200px;
    margin: auto;
    margin-top: 50px;
   
}

#score {
   align-items: center;
    display: flex;
    justify-content: center;
    width: 500px;
    height: 100px;
    margin: auto;
}
#player {
    margin-right: 200px;
}

#handbutton {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    max-width: 500px;
    button {
        margin-right: 20px;
    }
}

#hand {
    h3{
        text-align: center;
    }
    img{
        max-width: 200px;
        margin-right: 20px;
    }
    #player12{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto;
       
    }
}

#playername {
    width: 200px;
    margin: auto;
    margin-top: 40px;
}
